-----------------------------------------------*/
.hoverbox {
  position: relative;
  overflow: hidden;
}
.hoverbox .hoverbox-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  margin: 0 !important;
  display: flex;
}
.hoverbox .hoverbox-primary-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
}
.hoverbox .hoverbox-bg {
  background: rgba(0, 0, 0, 0.25);
  transition: background 0.2s ease-in-out;
}
.hoverbox .hvr-scale {
  transition: transform 0.2s ease-in-out;
  transition-delay: 0.1s;
}
.hoverbox:hover .hover-box-content-initial, .hoverbox:focus .hover-box-content-initial {
  opacity: 0;
}
.hoverbox:hover .hoverbox-content, .hoverbox:focus .hoverbox-content {
  opacity: 1;
}
.hoverbox:hover .as-hoverbox-content, .hoverbox:focus .as-hoverbox-content {
  z-index: 1;
}
.hoverbox:hover .hvr-scale, .hoverbox:focus .hvr-scale {
  transform: scale(1.2);
}

.hoverbox-bg {
  position: relative;
}
.hoverbox-bg::after, .hoverbox-bg::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.4s cubic-bezier(0.77, 0, 0.18, 1);
}
.hoverbox-bg::after {
  transform: scale(1, 0);
  border-left: 2px solid rgba(255, 255, 255, 0.75);
  border-right: 2px solid rgba(255, 255, 255, 0.75);
}
.hoverbox-bg::before {
  transform: scale(0, 1);
  border-top: 2px solid rgba(255, 255, 255, 0.75);
  border-bottom: 2px solid rgba(255, 255, 255, 0.75);
}

.hoverbox:hover .hoverbox-bg::after, .hoverbox:hover .hoverbox-bg::before, .hoverbox:focus .hoverbox-bg::after, .hoverbox:focus .hoverbox-bg::before {
  transform: scale(1);
}

